.BannerContainer {
    width: 20rem;
    height: 20rem;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    position: relative;
    top: 5.5rem;
    left: 2rem;
}

.BannerImage {
    background-image: url('../../assets/images/backgroundImage.jpg');
    background-repeat: no-repeat;
    bottom: 1.5px;
    left: 0;
    position: absolute;
    right: 0;
    top: 4.7rem;
    background-size: cover;
    border-top: 7px solid black;
    border-bottom: 7px solid black;

    /* opacity: 0.7; */
}

.BannerInfo {
    background: #3A4042;
    filter: drop-shadow(10px 10px 0px #000000);
    
    font-family: 'Press Start 2P', cursive;
    font-style: normal;
    font-weight: normal;
    font-size: 1.65rem;
    line-height: 2.5rem;
    width: 39.188rem;
    height: 11.5rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.bannerName {
    color: #006274;
    text-shadow: 4px 4px 0px #000000;

    position: relative;
    bottom: 1.2rem;
}

.bannerStack {
    color: #fff;
    text-shadow: 4px 4px 0px #000000;
}

.BannerButton {
    width: 39.188rem;
    display: flex;
    justify-content: space-around;
}

.BannerContainer button {
    background: #006274;
    font-family: 'Fira Code';
    font-weight: 500;
    font-size: 1.188rem;
    color: #fff;
    width: 8.95rem;
    height: 3.75rem;
    cursor: pointer;
    box-shadow: 0.375rem 0.25rem 0.063rem 0.063rem rgba(0,0,0,0.75);
    border: none;
}

.BannerButton button:hover {
    color: #d8cdcd;
    background: #025361;
}                                                                                                                                                   