* {
    border: 0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@media(max-width: 1080px){
    html {
        font-size: 93.75%; /* font-size to 15px */
    }
}

@media(max-width: 720px){
    html {
        font-size: 87.5%; /* font-size to 14px */
    }
}

.home-page, .description-page {
    height: 100vh;
}

.description-page {
    border-bottom: 0.625rem solid #000;
}

.projects-page {
    height: 32.5rem;
    background-color: #000;
}

.App {
    width: 100%;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

body {
    background:#007B77;
    font-family: 'Oswald', sans-serif;
}