.Header {
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 0.375rem;
}

.menuHeader {
    position: relative;
    right: 1.5rem;
}

.Header a {
    color: #fff;
    margin: 0 0.625rem;
    font-family: 'Fira Code';
    font-weight: 500;
    font-size: 1.25rem;
    text-decoration: none;
    letter-spacing: 0.075rem;
    cursor: pointer;
}

.Header a:hover {
    color: #d8cdcd;
}

.Header button {
    background: #006274;
    font-family: 'Fira Code';
    font-weight: 500;
    font-size: 1.188rem;
    color: #fff;
    width: 8.75rem;
    height: 3.75rem;
    cursor: pointer;
    box-shadow: 0.375rem 0.25rem 0.063rem 0.063rem rgba(0,0,0,0.75);
    border: none;
}

.Header button:hover {
    color: #d8cdcd;
    background: #025361;
}
