.button-arrow {
    width: 3.125rem;
    height: 3.125rem;
    background-color: #006274;
    cursor: pointer;

    box-shadow: 5px 5px 0px black;
    margin-left: 1rem;
}

.button-arrow .content {
    display: none;
}

.button-arrow::before {
    content: '';
    display: block;
    box-sizing: border-box;
    width: 0.3rem;
    height: 0.3rem;
    border: 1.02rem solid transparent;
    border-left-color: white;
    transform: translateX(1rem);
}

.button-arrow.-left::before {
    transform: rotate(180deg);
}