.skills-page {
    width: 100%;
    background: #006274;
    
    padding-top: 2.125rem;
    padding-bottom: 4.25rem;
    border-bottom: 0.625rem solid #007B77;
}

.main-title{
    display: flex;
    justify-content: center;

    font-family: 'Press Start 2P', cursive;
    color: #fff;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 40px;

    padding: 2rem;
    margin-bottom: 1.2rem;
}

.wrapper-skill {
    display: flex;
    flex-wrap: wrap;
    max-width: 60rem;

    margin-left: auto;
    margin-right: auto;
}

.skill {
    display: flex;
    align-items: center;

    width: 26.875rem;
    margin-bottom: 1rem;
}

.skill:nth-child(odd) {
    margin-right: 6.25rem;
}

.skill .icon {
    margin-right: 1.563rem;
    width: 14%;
}

.skill .title {
    font-family: 'Press Start 2P', cursive;
    color: #fff;
    font-size: 1.4rem;

    margin-bottom: 0.4rem;
}

.skill .right {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.progress-bar {
    border: 0.313rem solid #000;
    width: 20rem;  
    height: 2.5rem;
}

.progress-bar::-webkit-progress-bar {
    background-color: #Ffff;
}

.progress-bar::-moz-progress-bar {
    background-color: #007B77;
}

.progress-bar::-webkit-progress-value {
    background-color: #007B77;
}
